.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.task-form {
  margin: 20px 0;
}

.task-form input {
  padding: 8px;
  margin-right: 8px;
  border-radius: 4px;
  border: none;
}

.task-form button {
  padding: 8px 16px;
  background-color: #61dafb;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.task-list {
  list-style-type: none;
  padding: 0;
  width: 300px;
}

.task-list li {
  background-color: #444;
  margin: 8px 0;
  padding: 12px;
  border-radius: 4px;
  cursor: pointer;
}